import { request } from '../utils/request'

export function getCurve(data) {   //曲线分析
    return request({
        method: 'post',
        url: `count/zb`,
        data
    })
}


export function getPower(data) {   //正向有功
    return request({
        method: 'post',
        url: `report/etcp`,
        data
    })
}
export function getFactor(data) {   //功率因数
    return request({
        method: 'post',
        url: `report/cos`,
        data
    })
}
export function getMaxNeed(data) {   //最大需量
    return request({
        method: 'post',
        url: `report/pmax`,
        data
    })
}


export function exportReport(query) {   //导出电量报表
    return request({
        method: 'get',
        url: `export/report`,
        params: query
    })
}