<template>
    <div class="curve commpage">
        <div class="curve-ser-box">
            <el-form :inline="true" :model="queryForm" class="demo-form-inline">

                <el-form-item label="所属公司:" label-width="90px" class="min-item">
                    <el-select v-model="queryForm.company" placeholder="所属公司" value-key="id" @change="handlCompany">
                        <el-option v-for="item in companyOptions" :label="item.shortName" :value="item"
                            :key="item.code"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="所属平台:" label-width="90px" class="min-item">
                    <el-autocomplete ref="autocomplete" @focus="autocompleteFocusFlag=true"
                        @blur="autocompleteFocusFlag=false" v-model="queryForm.platName"
                        :fetch-suggestions="querySearchKp" placeholder="请选择平台或输入名称搜索" @select="handleSelectKp" clearable
                        @clear="clearKp">
                    </el-autocomplete>
                </el-form-item>
                <el-form-item label="计量点选择:" label-width="90px" class="max-item point-item">
                    <el-select v-model="queryForm.jpcode" placeholder="计量点选择" @change="handlPoint">
                        <el-option v-for="item in pointOptions" :label="item.name" :value="item.code" :key="item.code">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="选择属性:" label-width="90px" class="min-item">
                    <el-select clearable v-model="queryForm.attr1" placeholder="选择属性">
                        <el-option v-for="item in attrOptions" :label="item.label" :value="item.value"
                            :key="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="选择属性:" label-width="90px" class="min-item">
                    <el-select clearable v-model="queryForm.attr2" placeholder="选择属性">
                        <el-option v-for="item in attrOptions" :label="item.label" :value="item.value"
                            :key="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="起止时间:" label-width="90px" class="max-item">
                    <el-date-picker v-model="timeArr" type="datetimerange" range-separator="至" start-placeholder="开始日期"
                        end-placeholder="结束日期" :picker-options="pickerBeginDateBefore">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="数据密度:" label-width="90px" class="min-item">
                    <el-select clearable v-model="queryForm.md" placeholder="选择数据密度">
                        <el-option label="1分" value="1m"></el-option>
                        <el-option label="5分" value="5m"></el-option>
                        <el-option label="15分" value="15m"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div class="btn-box"><span @click="search">查 询</span><span @click="resetQuery">重 置</span></div>
        </div>

        <LineChart :class-name="'curve-echarts'" :char-type="isCompar?'compar':'line'" :chart-data="lineChartData" />
    </div>
</template>

<script>
    import * as echarts from 'echarts';
    import LineChart from '@/components/char'
    import Table from './table.vue'
    import moment from 'moment'
    import {
        getCompany,
        getPlat,
        getPoints
    } from '@/api/yw'
    //接口
    import {
        getKpList,
    } from "@/api/archives.js";
    import {
        getCurve
    } from '@/api/analyse'
    import {
        mapState
    } from 'vuex'
    export default {
        name: 'repor',
        components: {
            Table,
            LineChart
        },
        data() {
            return {
                autocompleteFocusFlag: false, //下拉搜索用
                companyOptions: [],
                /***
                 *  设置默认平台
                 *  四川页岩  阳101H91
                    蜀南气矿  自201H56平台
                    长宁  宁217评价井
                    重庆页岩气 足203H9
                    开发事业部 黄202H5
                    重庆气矿  黄203
                    川中油气矿 秋林集气总站（50万方）
                    新疆油田 JWH61
                    海南福山油田 福海1井
                 */
                defaultSelectedPlatName: ['阳101H91', '自201H56平台', '宁217评价井', '足203H9', '黄202H5', '黄203', '秋林集气总站（50万方）',
                    'JWH61', '福海1井'
                ],
                queryForm: {
                    company: '',
                    plat: '',
                    platName: '', //平台名称 用于下拉搜素显示
                    attr1: 'i', //默认选择正向有功
                    attr2: 'u',
                    jpcode: '', //计量点
                    st: '', //起始时间
                    et: '', //结束时间
                    md: '15m',
                },
                attrOptions: [{
                        value: 'i',
                        label: '电流'
                    }, {
                        value: 'u',
                        label: '电压'
                    }, {
                        value: 'p',
                        label: '有功功率'
                    }, {
                        value: 'q',
                        label: '无功功率'
                    },
                    {
                        value: 'fzRate',
                        label: '负载率'
                    },
                    {
                        value: 'cos',
                        label: '功率因数'
                    }
                ],
                pointOptions: [],
                timeArr: '', //pick选择后获取到的起止时间
                deflutPoint: '', //当前默认选中的计量点
                linesObj: {
                    i: {
                        name: '电流曲线',
                        unit: 'A',
                        lines: [{
                            key: 'mIa',
                            text: 'IA',
                            color: '#FFC62D'
                        }, {
                            key: 'mIb',
                            text: 'IB',
                            color: '#0AB975'
                        }, {
                            key: 'mIc',
                            text: 'IC',
                            color: '#EF4474'
                        }]
                    },
                    u: {
                        name: '电压曲线',
                        unit: 'V',
                        lines: [{
                            key: 'mUa',
                            text: 'UA',
                            color: '#FFC62D'
                        }, {
                            key: 'mUb',
                            text: 'UB',
                            color: '#0AB975'
                        }, {
                            key: 'mUc',
                            text: 'UC',
                            color: '#EF4474'
                        }]
                    },
                    p: {
                        name: '有功功率',
                        unit: 'kW',
                        lines: [{
                            key: 'mP',
                            text: '有功功率',
                            color: '#2E4AF7'
                        }]
                    },
                    q: {
                        name: '无功功率',
                        unit: 'kVar',
                        lines: [{
                            key: 'mQ',
                            text: '无功功率',
                            color: '#2E4AF7'
                        }]
                    },
                    pamx: {
                        name: '最大需量',
                        unit: 'kW',
                        lines: [{
                            key: 'mDmdmax',
                            text: '最大需量',
                            color: '#2E4AF7'
                        }]
                    },

                    cos: {
                        name: '功率因数',
                        lines: [{
                            key: 'mCos',
                            text: '功率因数',
                            color: '#2E4AF7'
                        }]
                    },
                    fzRate: {
                        name: '负载率',
                        unit: '%',
                        lines: [{
                            key: 'fzRate',
                            text: '负载率',
                            color: '#2E4AF7'
                        }]
                    },
                },
                lineChartData: {
                    legend: [],
                    dataRoomShow: false,
                    series: [],
                    yAxis: [],
                    xAxis: [],
                    colors: [],
                    name: [],
                    max1: '',
                    max2: '',
                },
                isCompar: false,
                lineOneData: [],
                lineTwoData: [],
                flag: true,
                lineProd1: false,
                lineProd2: false,
                pickerBeginDateBefore: {
                    disabledDate(time) {
                        return time.getTime() >= (Date.now());
                    }
                }
            }
        },
        created() {
            let st = moment(new Date()).format('YYYY-MM-DD 00:00:00')
            let et = new Date()
            this.timeArr = [st, et]
            this.getCompany()
        },
        mounted() {},
        methods: {
            //模糊搜索平台下拉
            querySearchKp(keyWord, cb) {
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    kcCode: this.queryForm.company.code,
                    _loading: false, //不显示加载中图标
                }
                if (keyWord !== '') {
                    opt.keyWord = keyWord;
                }
                getKpList(opt).then(res => {
                    if (res.data) {
                        const list = res.data.map(item => ({
                            ...item,
                            value: item.name,
                        }))
                        cb(list)
                    } else {
                        cb([])
                    }
                })
            },
            //平台下拉选择某项
            handleSelectKp(item) {
                //储存值
                this.queryForm.platName = item.name;
                this.queryForm.plat = item.code;
                //查询计量点
                this.getPoints();
            },
            //清空平台选择同时清空计量点
            clearKp() {
                this.queryForm.platName = '';
                this.queryForm.plat = '';
                this.pointOptions = [];;
                this.queryForm.jpcode = '';
                if (this.autocompleteFocusFlag) this.$refs.autocomplete.activated = true;
            },
            getCompany() {
                getCompany().then(res => { //获取全部公司
                    this.companyOptions = res.data;
                    this.queryForm.company = res.data.length > 0 ? res.data[0] : {}; //默认选择第一个公司
                    this.companyName = res.data.length > 0 ? res.data[0].fullName : '';
                    if (res.data.length > 0) this.getPlat();
                })
            },
            getPlat() { //获取当前选中公司下的全部的平台
                let query = {
                    kcCode: this.queryForm.company.code
                }
                getPlat(query).then(res => {
                    if (res.data && res.data.length > 0) {
                        const defaultPlat = res.data.find(item => this.defaultSelectedPlatName.includes(item
                            .name))
                        this.queryForm.plat = defaultPlat ? defaultPlat.code : res.data[0].code;
                        this.queryForm.platName = defaultPlat ? defaultPlat.name : res.data[0].name;
                        this.getPoints();
                    } else {
                        this.pointOptions = [];
                        this.queryForm.jpcode = '';
                        this.lineOneData = [];
                        this.lineTwoData = [];
                        this.initEcharts();
                    }
                })
            },
            getPoints() {
                let query = {
                    kpCode: this.queryForm.plat
                }
                getPoints(query).then(res => {
                    this.pointOptions = res.list.rows;
                    this.queryForm.jpcode = res.list && res.list.rows && res.list.rows.length > 0 ? res.list
                        .rows[0].code : ''
                    this.search();

                })
            },
            handlCompany() {
                this.companyName = this.queryForm.company.fullName,
                    this.getPlat();
            },
            handlPalt() {
                this.getPoints();
            },
            handlPoint() {
                this.search();
            },
            search() {
                if (!this.initFlag) {
                    this.initFlag = true;
                    return;
                }
                if (this.queryForm.plat == '') {
                    this.$message({
                        message: '请选择所属平台',
                        type: 'warning',
                    })
                    return
                }
                if (this.queryForm.jpcode == '') {
                    this.$message({
                        message: '请选择计量点',
                        type: 'warning',
                    })
                    return
                }
                if (this.queryForm.attr1 && this.queryForm.attr2) {
                    this.isCompar = true
                    this.getlineBoth();
                } else {
                    this.isCompar = false;
                    this.lineTwoData = [];
                    this.getlineOne();
                }
            },
            getlineOne() { //第一个曲线
                const data = {
                    type: this.queryForm.attr1 ? this.queryForm.attr1 : this.queryForm.attr2,
                    jpcode: this.queryForm.jpcode,
                    part: this.queryForm.md,
                    st: this.timeArr[0],
                    et: this.timeArr[1],
                }
                getCurve(data).then(res => {
                    this.lineOneData = res.data;
                    this.initEcharts();
                })
            },
            getlineBoth() {
                const data = {
                    jpcode: this.queryForm.jpcode,
                    part: this.queryForm.md,
                    st: this.timeArr[0],
                    et: this.timeArr[1]
                }
                const data1 = Object.assign({}, data);
                const data2 = Object.assign({}, data);
                data1.type = this.queryForm.attr1;
                data2.type = this.queryForm.attr2;
                const p1 = new Promise((resolve, reject) => {
                    return getCurve(data1).then(res => {
                        resolve(res.data)
                    })
                })
                const p2 = new Promise((resolve, reject) => {
                    return getCurve(data2).then(res => {
                        resolve(res.data)
                    })
                })
                Promise.all([p1, p2]).then(res => {
                    // console.log(res)
                    this.lineOneData = res[0] ? res[0] : [];
                    this.lineTwoData = res[1] ? res[1] : [];
                    this.initEcharts();
                }).catch(err => {
                    alert(err);
                })

            },
            initEcharts() {
                // this.myChart = echarts.init(document.getElementById('myChart'));
                this.lineChartData.colors = [];
                this.lineChartData.xAxis = [];
                this.lineChartData.yAxis = [];
                this.lineChartData.legend = [];
                this.lineChartData.series = [];
                this.lineChartData.name = [];

                this.lineProd1 = false;
                this.lineProd2 = false;
                this.lineChartData.max1 = '';
                this.lineChartData.max2 = '';
                let options = {}
                if (this.isCompar) {
                    let line1 = [],
                        line2 = [],
                        line = []

                    line1 = this.linesObj[this.queryForm.attr1].lines;
                    line2 = this.linesObj[this.queryForm.attr2].lines;
                    line = line1.concat(line2);
                    line.map((item => {
                        this.lineChartData.legend.push(item.text);
                        this.lineChartData.colors.push(item.color);
                    }))
                    if (this.queryForm.attr1 === 'fzRate') this.lineChartData.max1 = 150;
                    if (this.queryForm.attr2 === 'fzRate') this.lineChartData.max2 = 150;

                    line1.map((item, index) => {
                        const series = [];
                        this.lineOneData.map((it, ind) => {
                            if (index === 0) {
                                if (ind == 0) {
                                    this.lineChartData.xAxis.push(moment(it.ts).format(
                                        'YY-MM-DD HH:mm'));
                                } else {
                                    this.lineChartData.xAxis.push(moment(it.ts).format(
                                        'MM-DD HH:mm'));
                                }

                            }
                            if (this.queryForm.attr1 === 'u') { //属性为电压的
                                if (it[item.key] > 1000) {
                                    series.push(it[item.key] || it[item.key] == 0 ? (it[item.key] /
                                        1000) : '');
                                    this.lineProd1 = true;
                                } else series.push(it[item.key])
                            } else if (this.queryForm.attr1 === 'fzRate') {
                                series.push(it[item.key] || it[item.key] == 0 ? (it[item.key] * 100)
                                    .toFixed(2) :
                                    '')
                            } else {
                                series.push(it[item.key]);
                            }

                        })

                        this.lineChartData.series.push({
                            name: item.text,
                            type: 'line',
                            // symbolSize: 8,
                            symbol: 'none',
                            smooth: true,
                            hoverAnimation: false,
                            data: series,
                            areaStyle: this.queryForm.attr1 !== 'fzRate' ? {
                                opacity: 0.05
                            } : {
                                opacity: 0
                            },
                            markArea: this.queryForm.attr1 === 'fzRate' ? {
                                data: [
                                    [{
                                        yAxis: '60',
                                        itemStyle: {
                                            color: 'green',
                                            opacity: 0.08
                                        }
                                    }, {
                                        yAxis: '75'
                                    }],
                                    [{
                                        yAxis: '100',
                                        itemStyle: {
                                            color: 'red',
                                            opacity: 0.05
                                        }
                                    }, {
                                        yAxis: '150'
                                    }]


                                ]
                            } : {
                                opacity: 0.05
                            },
                        })
                    })

                    line2.map(item => {
                        const series = [];
                        this.lineTwoData.map(it => {
                            if (this.queryForm.attr2 === 'u') { //属性为电压的
                                if (it[item.key] > 1000) {
                                    series.push(it[item.key] || it[item.key] == 0 ? (it[item.key] /
                                        1000) : '')
                                    this.lineProd2 = true;
                                } else series.push(it[item.key])
                            } else if (this.queryForm.attr2 === 'fzRate') {
                                series.push(it[item.key] || it[item.key] == 0 ? (it[item.key] * 100)
                                    .toFixed(2) :
                                    '')
                            } else {
                                series.push(it[item.key])
                            }

                        })

                        this.lineChartData.series.push({
                            name: item.text,
                            type: 'line',
                            xAxisIndex: 1,
                            yAxisIndex: 1,
                            symbol: 'none',
                            smooth: true,
                            hoverAnimation: false,
                            data: series,
                            areaStyle: this.queryForm.attr2 !== 'fzRate' ? {
                                opacity: 0.05
                            } : {
                                opacity: 0
                            },
                            markArea: this.queryForm.attr2 === 'fzRate' ? {
                                data: [
                                    [{
                                        yAxis: '60',
                                        itemStyle: {
                                            color: 'green',
                                            opacity: 0.08
                                        }
                                    }, {
                                        yAxis: '75'
                                    }],
                                    [{
                                        yAxis: '100',
                                        itemStyle: {
                                            color: 'red',
                                            opacity: 0.05
                                        }
                                    }, {
                                        yAxis: '150'
                                    }]


                                ]
                            } : {
                                opacity: 0.05
                            },
                        })
                    })
                    this.lineChartData.name[0] = this.linesObj[this.queryForm.attr1].name ? this.linesObj[this.queryForm
                        .attr1].name + (this.linesObj[this.queryForm.attr1].unit ? `(${this.linesObj[this.queryForm
                        .attr1].unit})` : '') : '';
                    this.lineChartData.name[1] = this.linesObj[this.queryForm.attr2].name ? this.linesObj[this
                        .queryForm.attr2].name + (this.linesObj[this.queryForm.attr2].unit ? `(${this.linesObj[this.queryForm
                        .attr2].unit})` : '') : '';


                    if (this.lineProd1) this.lineChartData.name[0] = this.linesObj[this.queryForm.attr1].name + '(kV)'
                    if (this.lineProd2) this.lineChartData.name[1] = this.linesObj[this.queryForm.attr2].name + '(kV)'
                } else {
                    let line1 = [];
                    line1 = this.linesObj[this.queryForm.attr1 ? this.queryForm.attr1 : this.queryForm.attr2].lines
                    let lineAttr = this.queryForm.attr1 ? this.queryForm.attr1 : this.queryForm.attr2;
                    let unit = '';
                    if (this.linesObj[lineAttr].unit) {
                        unit = `(${this.linesObj[lineAttr].unit})`
                    }
                    this.lineChartData.name[0] = this.linesObj[lineAttr].name + unit

                    if (lineAttr === 'fzRate') this.lineChartData.max1 = 150;

                    line1.map((item, index) => {
                        const series = [];

                        this.lineChartData.legend.push(item.text)
                        this.lineChartData.colors.push(item.color)
                        this.lineOneData.map((it, ind) => {
                            if (ind == 0) {
                                this.lineChartData.xAxis.push(moment(it.st).format(
                                    'YY-MM-DD HH:mm'))
                            } else {
                                this.lineChartData.xAxis.push(moment(it.st).format(
                                    'MM-DD HH:mm'))
                            }
                            if (lineAttr === 'u') { //属性为电压的
                                if (it[item.key] > 1000) {
                                    series.push(it[item.key] || it[item.key] == 0 ? (it[item.key] /
                                        1000) : '')
                                    this.lineProd1 = true;
                                } else series.push(it[item.key])
                            } else if (lineAttr === 'fzRate') {
                                series.push(it[item.key] || it[item.key] == 0 ? (it[item.key] * 100)
                                    .toFixed(2) :
                                    '')

                            } else {
                                series.push(it[item.key])
                            }

                        })
                        if (this.lineProd1) this.lineChartData.name[0] = this.linesObj[lineAttr].name + '(kV)'
                        this.lineChartData.series.push({
                            name: item.text,
                            type: 'line',
                            smooth: true,
                            data: series,
                            markArea: lineAttr === 'fzRate' ? {
                                data: [
                                    [{
                                        yAxis: '60',
                                        itemStyle: {
                                            color: 'green',
                                            opacity: 0.08
                                        }
                                    }, {
                                        yAxis: '75'
                                    }],
                                    [{
                                        yAxis: '100',
                                        itemStyle: {
                                            color: 'red',
                                            opacity: 0.05
                                        }
                                    }, {
                                        yAxis: '150'
                                    }]


                                ]
                            } : {},
                        })
                    })

                }
            },
            resetQuery() {
                this.queryForm = {
                    attr1: 'i', //默认选择正向有功
                    attr2: 'u',
                    jpcode: this.deflutPoint, //计量点
                    st: '', //起始时间
                    et: '', //结束时间
                }
                let st = moment(new Date()).format('YYYY-MM-DD 00:00:00')
                let et = new Date()
                this.timeArr = [st, et]
                this.lineChartData = {
                    legend: [],
                    dataRoomShow: false,
                    series: [],
                    yAxis: [],
                    xAxis: [],
                    colors: [],
                    name: [],
                };
                this.isCompar = false;
                this.lineOneData = [];
                this.lineTwoData = [];
                this.search()
            }
        },
        //keepalive 加载组件事件 再次加载数据
        activated: function () {
            this.search();
        },
        //keepalive 离开组件事件，清空数据
        deactivated: function () {
            this.lineChartData.series = []
        }
    }
</script>

<style scoped lang="scss">
    .curve {
        background: #fff;
        box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
        border-radius: 6px;
        padding: 0.25rem 0.19rem 0.5rem 0.24rem;
        box-sizing: border-box;
    }

    .curve-ser-box {
        position: relative;
        font-size: 0.14rem;
        display: flex;
        justify-content: space-between;
    }

    .curve-pick {
        ::v-deep .el-range-editor.el-input__inner {
            padding: 0 10px;
        }

        ::v-deep .el-date-editor .el-range-separator {
            padding: 0 5px;
            line-height: 29px;
            width: 5%;
            color: #303133;
        }

        ::v-deep .el-input__inner {
            line-height: 0.32rem;
            height: 0.32rem;
        }
    }

    .curve-select {
        width: 2.37rem;
        height: 0.32rem;
        margin-right: 0.3rem;

        ::v-deep .el-input__inner {
            line-height: 0.32rem;
            height: 0.32rem;
        }
    }


    .common-font {
        font-size: 0.14rem;
        display: flex;
        justify-content: space-between;
    }

    .btn-box {
        position: absolute;
        right: 0;
    }

    .btn-box span {
        width: 0.65rem;
        height: 0.32rem;
        text-align: center;
        line-height: 0.32rem;
        border-radius: 4px;
        display: inline-block;
        cursor: pointer;
